  display: flex;
  border-bottom: 2px solid #dee2e6;
  padding-left: 0;
  margin-bottom: 16px;
}

.nav-item {
  list-style: none;
}

.nav-item a {
  display: inline-flex;
  align-items: center;
  padding: 10px 16px;
  font-size: 1em;
  font-weight: 500;
  color: #555;
  text-decoration: none;
  border-bottom: 2px solid transparent;
  background: none;
  cursor: pointer;
  transition: color 0.2s ease, border-color 0.2s ease;
}

.nav-item a:hover {
  color: #007BFF;
  border-bottom: 2px solid #cce5ff;
}

.nav-item a.is-active {
  color: #007BFF;
  border-bottom: 2px solid #007BFF;
  font-weight: 600;
}

.nav-item a .ml-10 {
  margin-left: 8px;
  font-size: 1.1em;
  font-weight: bold;
  color: #999;
  transition: color 0.2s ease;
}

.nav-item a .ml-10:hover {
  color: #dc3545;
