  display: flex;
  align-items: center;
  gap: 8px;
  width: 100%;
  padding: 20px 0;
}

.icon {
  font-size: 20px;
  margin-right: 4px;
}

.zipcode-input {
  flex: 1;
  border: none;
  border-bottom: 1.5px solid #999;
  padding: 10px 8px;
  font-size: 16px;
  background: transparent;
  outline: none;
  transition: border-color 0.3s;
}

.zipcode-input:focus {
  border-bottom-color: #5c6bc0;
}

