  background: none;
  border: 1.5px solid #5c6bc0;
  color: #5c6bc0;
  padding: 8px 20px;
  font-size: 14px;
  border-radius: 20px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.zipcode-button:hover {
  background-color: #5c6bc0;
  color: white;
