___CSS_LOADER_EXPORT___.push([module.id, `.weather-card-horizontal {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 20px;
    padding: 24px;
    margin: 20px 0;
    color: #fff;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    cursor: pointer;
    opacity: 0;
    transform: translateY(12px);
    animation: fadeInUp 0.6s ease forwards;
    -webkit-backdrop-filter: blur(8px);
            backdrop-filter: blur(8px);
}

.weather-card-horizontal:hover {
    transform: translateY(-4px);
    box-shadow: 0 12px 28px rgba(0, 0, 0, 0.15);
}

.bg-clear {
    background: linear-gradient(135deg, #4facfe, #00f2fe); /* Blue sky */
}

.bg-clouds {
    background: linear-gradient(135deg, #d7d2cc, #304352); /* Gray tones */
}

.bg-rain {
    background: linear-gradient(135deg, #bdc3c7, #2c3e50); /* Dark rain */
}

.bg-thunderstorm {
    background: linear-gradient(135deg, #434343, #000000); /* Dark storm */
}

.bg-snow {
    background: linear-gradient(135deg, #e0eafc, #cfdef3); /* Light snow */
}

.bg-mist {
    background: linear-gradient(135deg, #cfd9df, #e2ebf0); /* Soft mist */
}

.bg-default {
    background: linear-gradient(135deg, #f2f2f2, #ccc); /* Neutral fallback */
}

.weather-icon-box img {
    width: 100px;
    height: 100px;
    animation: float 4s ease-in-out infinite;
    transition: transform 0.3s ease;
}

.weather-card-horizontal:hover .weather-icon-box img {
    transform: scale(1.05);
}

.weather-main-info {
    flex-grow: 1;
    padding: 0 24px;
}

.city {
    font-size: 1.8em;
    margin: 0;
}

.zip {
    font-size: 0.95em;
    opacity: 0.8;
}

.condition {
    font-weight: 600;
    margin: 10px 0;
}

.temps-row {
    display: flex;
    gap: 16px;
    font-size: 1.1em;
}

.weather-extras {
    text-align: right;
    font-size: 0.95em;
}

.weather-extras p {
    margin: 4px 0;
}

.forecast-link {
    display: inline-block;
    margin-top: 8px;
    color: #fff;
    font-weight: 500;
    text-decoration: none;
    transition: color 0.2s ease;
}

.forecast-link:hover {
    text-decoration: underline;
    color: #e0e0ff;
}

@keyframes fadeInUp {
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes float {
    0%, 100% {
        transform: translateY(0);
    }
    50% {
        transform: translateY(-6px);
    }
}

@media (max-width: 768px) {
    .weather-card-horizontal {
        flex-direction: column;
        align-items: flex-start;
    }

    .weather-main-info {
        padding: 16px 0;
    }

    .weather-extras {
        text-align: left;
        width: 100%;
    }
}

.temp-icon {
    display: inline-block;
    margin-right: 6px;
    font-size: 1.2em;
}

@keyframes pulse {
    0%, 100% {
        transform: scale(1);
        opacity: 1;
    }
    50% {
        transform: scale(1.1);
        opacity: 0.9;
    }
}

.pulse {
    animation: pulse 2.5s ease-in-out infinite;
}

@keyframes bounceUp {
    0%, 100% {
        transform: translateY(0);
    }
    50% {
        transform: translateY(-3px);
    }
}

.bounce-up {
    animation: bounceUp 2.8s ease-in-out infinite;
}

@keyframes bounceDown {
    0%, 100% {
        transform: translateY(0);
    }
    50% {
        transform: translateY(3px);
    }
}

.bounce-down {
    animation: bounceDown 2.8s ease-in-out infinite;
}


.weather-card-horizontal {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 20px;
    padding: 32px;
    margin: 24px 0;
    min-height: 220px;
    color: #fff;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    cursor: pointer;
    opacity: 0;
    transform: translateY(12px);
    animation: fadeInUp 0.6s ease forwards;
    -webkit-backdrop-filter: blur(8px);
            backdrop-filter: blur(8px);
}

weather-icon-box img {
    width: 110px;
    height: 110px;
}

.loader-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 40vh;
    text-align: center;
}

.spinner {
    width: 48px;
    height: 48px;
    border: 5px solid #f3f3f3;
    border-top: 5px solid #007bff;
    border-radius: 50%;
    animation: spin 1s linear infinite;
    margin-bottom: 1rem;
}

.loading-text {
    font-size: 1.1rem;
    font-weight: 500;
    color: #555;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

